.main-card-container{
    width: 100%;
    height: 100%;
    text-decoration: none;
    display: inline-block;
}
.card-container{
    position: relative;
    @media screen and (max-width:600px){
        margin:10px 0;
    }
    .purchased{
        @media screen and (max-width:600px) {
            top: 280px;
            padding: .35rem 1rem;
            background-color: white;
            color: #686868;
            border: 1px solid #686868;
        }
        color: #616161;
        background: rgba(248,249,250,.95);
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        font-size: .75rem;
        letter-spacing: -.6px;
        line-height: 1;
        padding: .2rem .38rem;
        position: absolute;
        top: 133px;
        right: 7px;
        z-index: 50;
    }
    text-align: right;
    border: 1px solid #e1e3df;
    border-radius: 4px;
    background-color: white;
    transition: all .5px;
    margin: 8px;
    &:hover{
        transform: translateY(2px);
    }
    hr{
        &:last-child{
            width: 92%;
            margin: 0 auto;
        }
    }
    .card-image{
        z-index: 40;
        border-bottom: 1px solid rgba(146, 146, 146, 0.075);
        width: 100%;
    }
    .card-title{
        margin: 8px 7px 0px 7px;
        color: #333;
        font-size: .95rem;
    }
    .card-subtitle{
        margin: 5px 7px 10px 7px;
        font-size: .75rem;
        color: #616161;
    }
}
.soldout-block{
    @media screen and (max-width:600px){
        display: none;
    }
    img{
        width: 120px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 5;
    }
    position: relative;
    display: block;
}
.soldout-none{
    display: none;
}
.car-icon{
    float:left;
    margin: -17px 15px;
}
